@import url(main.css);

.formContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
}

.formContainer form {
    width: 30%;
    padding: 2rem;
    border-radius: .7rem;
    box-shadow: 0 0 10px rgba(0,0,0, 0.5);
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
}

.formContainer form .formGroup {
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
    margin-top: 2rem;
}

.formContainer form .formGroup input {
    padding: .6rem;
    outline: none;
    border: 1px solid #ccc;
    font-family: var(--font-family-poppins);
}

.formContainer form .formGroup .passwordInput {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: .2rem;
    padding-right: .3rem;
}



.formContainer form .formGroup .passwordInput input{
    width: 100%;
    border: none;
    border-radius: .2rem;
}


.formContainer form .formGroup .passwordInput i {
    font-size: 1.3rem;
    color: var(--blue-color-400);
    cursor: pointer;
}

.formContainer form button {
    margin-top: 1rem;
    padding: .4rem;
    border: none;
    font-family: var(--font-family-poppins);
    border-radius: .4rem;
    background: var(--blue-color-400);
    color: #fff;
    cursor: pointer;
    font-weight: 700;
    font-size: 1.2rem;
}

.formContainer form button:hover {
    background: var(--blue-color-500);
}