*,
input::placeholder {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font-family-poppins);

}

main {
    margin-top: 5rem;
    padding: 0 2rem;
}

body {
    font-family: var(--font-family-poppins);
    font-size: 700;
}

/* navbar */


.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background: var(--gray-color-100);
    z-index: 99999999;
}

.navbar nav {
    width: 90%;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;

}

.navbar nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 2rem;
    padding: .5rem 0;
}

.navbar .burger {
    display: none;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 95%;
    cursor: pointer;
    transition: 0.3s;
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
    z-index: 8;
}

.navbar nav .burger .line1,
.navbar nav .burger .line2,
.navbar nav .burger .line3 {
    background: var(--dark-color);
    width: 100%;
    height: 3px;
    transition: 0.3s;
    border-radius: 2px;
}

.navbar nav .burger .line1 {
    position: absolute;
    top: 17px;
    left: 50%;
    transform: translateX(-50%);
}


.navbar nav .burger .line3 {
    position: absolute;
    bottom: 17px;
    left: 50%;
    transform: translateX(-50%);
}


.navbar nav .burger .line1.change {
    transform: translateX(-50%) rotate(45deg);
    top: 22px;
}

.navbar nav .burger .line2.change {
    opacity: 0;
}


.navbar nav .burger .line3.change {
    transform: translateX(-50%) rotate(-45deg);
    bottom: 24px;
}


.navbar .logo {
    color: var(--dark-color);
    font-weight: 670;
    text-decoration: none;
    font-size: 1.4rem;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-style-1 ul li a {
    color: var(--dark-color);
    font-weight: 600;
    text-decoration: none;
}

.navbar-style-1 ul li a:hover {
    color: var(--gray-color-700);
}

.navbar-style-2 ul li a {
    color: var(--dark-color);
    font-weight: 600;
    text-decoration: none;
    position: relative;
}

.navbar-style-2 ul li a::after {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    width: 0;
    height: 2px;
    background: var(--gray-color-700);
}

.navbar-style-2 ul li a:hover::after {
    width: 100%;
}

.navbar-style-3 ul li a {
    color: var(--dark-color);
    font-weight: 600;
    text-decoration: none;
}

.navbar-style-3 ul li {
    padding: 0.2rem 1rem;
    border-radius: var(--border-radius-3xl);
}


.btnSign {
    background: var(--blue-color-400);
    padding: .4rem .7rem;
    border-radius: .3rem;
}

/* Home page */

.homePage {
    background-image: url(https://www.telegraph.co.uk/content/dam/health-fitness/2022/11/04/TELEMMGLPICT000314850591_trans_NvBQzQNjv4BqgsaO8O78rhmZrDxTlQBjdLdu0TL-Cg_AMOUqySXmFgU.jpeg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    backdrop-filter: blur(5px);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.homePage .content{
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    color: #fff;
    background: rgba(0, 0, 0, 0.5);
    width: 30%;
    border-radius: .7rem;
    padding: 3rem;
}

.homePage .content a {
    color: #fff;
    background: var(--blue-color-400);
    border-radius: .3rem;
    text-decoration: none;
    font-weight: 700;
    padding: .4rem;
    margin-top: 1rem;
}

@media screen and (max-width:965px) {


    .navbar .burger {
        display: flex;
    }

    .navbar nav ul {
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 9;
        height: 100vh;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        padding: 2rem 3rem;
        gap: .5rem;
        background: var(--light-gray-color);
        width: 50%;
        padding-top: 7rem;
        border-radius: var(--border-radius-rounded);
    }

    .navbar nav ul.slide {
        border-radius: 0;
        left: 0;
    }
}
